 class=\"books\" v-if=\"this.books!=null\">\n          <el-table\n            :data=\"this.books\"\n            height=\"700px\"\n            border\n            style=\"width: 1000px;\">\n            <el-table-column label=\"封面\" width=\"120\">\n              <template slot-scope=\"scope\">\n                <el-image :src=\"'http://localhost:8088/upload/'+scope.row.imgurl\"  style=\"width: 90px;height: 90px\"></el-image>\n              </template>\n            </el-table-column>\n            <el-table-column\n              prop=\"iSBN\"\n              label=\"ISBN\"\n              width=\"180\">\n            </el-table-column>\n            <el-table-column\n              prop=\"bookName\"\n              label=\"书名\"\n              width=\"180\">\n            </el-table-column>\n            <el-table-column\n              prop=\"author\"\n              width=\"180\"\n              label=\"作者\">\n            </el-table-column>\n            <el-table-column\n              prop=\"publisher\"\n              width=\"180\"\n              label=\"出版社\">\n            </el-table-column>\n            <el-table-column\n              prop=\"price\"\n              width=\"180\"\n              label=\"价格\">\n            </el-table-column>\n            <el-table-column\n              prop=\"storage\"\n              label=\"库存\">\n            </el-table-column>\n            <el-table-column\n              prop=\"tips\"\n              width=\"250\"\n              label=\"描述\">\n            </el-table-column>\n            <el-table-column\n              fixed=\"right\"\n              label=\"操作\"\n              width=\"100\">\n              <template slot-scope=\"scope\">\n                <el-button @click=\"updateBook(scope.row.bookId)\" type=\"text\" size=\"small\">编辑</el-button>\n                <el-button @click=\"delBook(scope.row)\" type=\"text\" size=\"small\">删除</el-button>\n              </template>\n            </el-table-column>\n          </el-table>\n\n        </div>\n        <div v-else>\n          <span>未发布闲置</span>\n        </div>\n\n      </div>\n\n    </div>\n</template>\n\n<script>\n  import {delbook, findbookbyid, findbooksbyuser} from \"../../../axios/api\";\n\n  export default {\n        name: \"goodlist\",\n      data(){\n          return{\n            books:[],\n            formInline: {\n              user: '',\n              region: ''\n            }\n      }\n      },\n      mounted() {\n        this.showBooks()\n      },\n    methods:{\n      onSubmit() {\n        console.log('submit!');\n      },\n      showBooks(){\n            let userId=this.$store.state.user.userId;\n            findbooksbyuser(userId).then(res=>{\n              if(res.data!=null){\n                console.log(res.data);\n                let imglist=[];\n                res.data.forEach(data=>{\n                  imglist=data.imgurl.split(\",\");\n                  data.imgurl=imglist[0]\n                })\n                this.books=res.data;\n              }\n            })\n          },\n      updateBook(bookId){\n      //跳转到商品修改页面\n        this.$router.push({\n          name:\"updatebook\",\n          params:{\n            bookId:bookId\n          }\n        });\n      },\n      delBook(book){\n        delbook(book).then(res=>{\n            if(res.data!=0){\n              this.$message({\n                showClose:true,\n                message:\"删除成功\",\n                type:\"success\"\n              })\n              this.showBooks()\n            }\n        })\n      }\n    }\n    }\n</script>\n\n<style scoped>\n.book{\n  width: 800px;\n  height: 180px;\n  border: 1px solid #000000;\n  padding: 10px;\n  margin-left: 60px;\n}\n  .books-list{\n    margin-left: 30px;\n  }\n\n/deep/ .el-button--primary{\n  background-color: #E0B41B;\n  border: 1px solid #E0B41B;\n  margin-left: 10px;\n  width: 100px;\n}\n  /deep/ .el-select .el-input.is-focus .el-input__inner{\n    border: 1px solid #E0B41B;\n  }\n\n  /deep/ .el-select .el-input__inner:focus{\n    border-color: #666752;\n  }\n  /deep/ .el-select-dropdown__item.selected{\n    color: #E0B41B;\n  }\n/deep/ .el-button--text{\n  color:#666752;\n}\n</style>\n"],"sourceRoot":""}]);

